#languages{
    width: 100%;
    display: grid;
    grid-template-columns:  1fr 1fr;
    grid-gap: 30px;
    position: relative;
}

.languages-box h3{
    font-size: 1.3rem;
    letter-spacing: 1px;
}

.languages-box ul{
    display: flex;
    flex-wrap: wrap;
    background: rgba(0, 0, 0, 0.08);
    padding: 10px 15px;
    margin-top: 10px;
}

.languages-box ul li{
    color: var(--grayColor);
    margin-right: 15px;
    margin-top: 4px;
}

@media (max-width: 665px){
    #language{
        grid-template-columns: 1fr;
    }
}