:root{
    --bgColor: #3a6760;
    --whiteColor: #12211f;
    --grayColor: #b8d7cf;
    --blueColor: #6f978d;
}

/* :root{
    --bgColor: #fdfdfd;
    --whiteColor: #000;
    --grayColor: #bbb;
    --blueColor: #689af8;
} */

*{
    margin: 0;
    padding: 0;
    font-family: "Poppins", serif;
    scroll-behavior: smooth;
    box-sizing: border-box;
}

body{
    width: 100%;
    background: var(--bgColor);
    position: relative;
    Color: var(--whiteColor);
}

.container{
    width: 100%;
    max-width: 1024px;
    min-height: 100vh;
    position: absolute;
    top: 0;
    left: 50%;
    transform: translate(-50%);
    padding:  0 15px;
    overflow: hidden;
}

a{
    text-decoration: none;
}

.btn-link{
    color: var(--whiteColor);
    text-transform: uppercase;
    font-weight: 400;
    letter-spacing: 5px;
    font-size: 0.8rem;
    border-bottom: 1px solid #dcebe7;
    padding: 10px 10px 10px 0;
    transition: all 0.5s cubic-bezier(0.075,0.82,0.165,1);
    cursor: pointer;
}

.btn-link:hover{
    padding: 10px;
    background: #8fbcb2;
}

section{
    margin-top: 30px;
    margin-bottom: 100px;
}

ul{
    list-style: none;
}

h3{
    font-size: 3rem;
    letter-spacing: 2px;
}

::-webkit-scrollbar{
    display: none;
}